.navbar-icon-top .navbar-nav .nav-link > .fa {
  position: relative;
  width: 36px;
  font-size: 24px;
  > .badge {
    font-size: 0.75rem;
    position: absolute;
    right: 0;
    font-family: sans-serif;
    top: -10px;
  }
  top: 3px;
  line-height: 12px;
}

.nav-item {
  font-size: 14px;
}

.search-form {

  width: 12%;
  margin-right: 10px;
  transition: width .6s ease;

  &:focus-within {
    width: 50%;
  }

  i {
    position: absolute;
    color: #fff;
    margin-left: 8px;
    opacity: 0.6;
  }

  input {
    border: none;
    width: 100% !important;
    background-color: #333131;
    border-radius: 0;
    //padding-left: 30px;
    padding: 22px 0 22px 30px;
    font-size: 15px;
    width: 100%;

    &:focus {
      background-color: #333131;
      box-shadow: none;
      color: #fff;
    }
  }
}

.dropdown-user-profile {
  
    img {
          width: 50px;
          height: 50px;
          margin-right: 8px;
          border-radius: 50%;
    }

    .dropdown-menu {
          display: block;
          position: absolute;
          top: 76px;
          right: -16px;
          left: initial;
          width: 240px;
          border-radius: 0 0 4px 4px;
          padding: 0;
          border: none;
          max-height: 0;
          overflow: hidden;
          box-shadow: -2px 2px 5px 0px rgba(0,0,0,0.15);
          transition: max-height .4s cubic-bezier(0, 1, 0.5, 1);

          &.show {
            max-height: 600px;
          }

          .dropdown-item {
            font-size: 14px;
            transition: padding-left .4s ease;

            &:hover, &:focus {
              background-color: rgba(240, 135, 39, 0.9);;
              color: #fff;
              padding-left: 30px;
            }

            i {
              margin-right: 4px;
            }
          }

          .dropdown-divider {
            margin: 2px 0;
          }
    }

}

