.member-area {
  color: #fff;
  padding: 2% 0;
  background-image: linear-gradient(to bottom, #343a40, #3a374b, #522e46, #64252b, #5e2d00);

  .row {

    .col {
      padding: 0 20px;
    }
  }

   h5 {
      font-size: 16px;
      font-weight: bold;
      margin-bottom: 18px;
    }

     .member-action {
      background-color: #FF8719;
      width: 100%;
      border: none;
      padding: 10px 0;
      color: #fff;
      border-radius: 4px;
      opacity: .94;
      cursor: pointer;
      transition: opacity .5s ease;

        &:hover, &:focus {
          opacity: 1;
        }
     }

  .col-login {

    .form-group {
      margin-bottom: 0;

      input {
        background: none;
        padding: 6px 0 0 24px;
        border: none;
        border-radius: 0;
        box-shadow: none;
        height: initial;
        font-size: 15px;
        color: rgba(255,255,255,0.7);
        border-bottom: 1px solid rgba(255,255,255,.5);
      }


       i {
        position: relative;
        top: -26px;
        left: 2px;
        font-size: 20px;
        font-size: 20px;
        color: #FF8719;
       }
    }

    .invalid-feedback {
      margin: -20px 0 12px 0;
    }

  }

  .col-register {
    p {
      opacity: .9;
      font-size: 13px;
      margin-bottom: 25px;
    }

    .btn-register {
        padding: 10px 20px;
    }
  }
}

@media (min-width: 576px) {
  .navbar-icon-top.navbar-expand-sm .navbar-nav .nav-link {
    text-align: center;
    display: table-cell;
    height: 70px;
    vertical-align: middle;
    padding-top: 0;
    padding-bottom: 0;
    > .fa {
      display: block;
      width: 48px;
      margin: 2px auto 4px auto;
      top: 0;
      line-height: 24px;
      > .badge {
        top: -7px;
      }
    }
  }
}

